.search-form {
  height: 60px;
  background-color: #deb887;
  justify-content: center;
  gap: 20px;
  padding: 10px;
  display: flex;
}

.search-form input {
  height: 40px;
  width: 300px;
  border: none;
  border-radius: 4px;
  font-style: italic;
  transition: transform .25s ease-in-out;
}

.search-form input:focus, .search-form input:hover {
  border: 1px solid;
  transform: scale(1.05);
}

.search-form button {
  height: 40px;
  width: 80px;
  background-color: #fff;
  border: 1px solid;
  border-radius: 4px;
  font-weight: 400;
  transition: transform .25s ease-in-out;
}

.search-form button:focus, .search-form button:hover {
  color: #deb887;
  border: none;
  outline: none;
  transform: scale(1.05);
}

.gallery {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  padding: 20px;
  display: flex;
}

.photo-card {
  width: calc(25% - 7.5px);
  height: auto;
  border: 2px solid rgba(222, 184, 135, .3);
  border-radius: 5px;
  padding: 5px 5px 0;
  transition: transform .25s ease-in-out;
}

.photo-card img {
  width: 100%;
  max-height: 210px;
  height: 100%;
  display: block;
}

.photo-card:hover {
  border: none;
  transform: scale(1.05);
  box-shadow: -5px 3px 5px 2px rgba(0, 0, 0, .3);
}

.info {
  font-size: 14px;
  display: flex;
}

.info-item {
  margin: 0;
  padding: 10px;
}

.info-item:not(:last-child) {
  border-right: 1px solid #deb887;
}

.load-more {
  height: 100px;
  width: 100px;
  background-color: #fff;
  border-radius: 50%;
  margin: 20px auto;
  padding: 2px;
  font-weight: 400;
  transition: transform .25s ease-in-out;
  display: block;
}

.load-more:focus, .load-more:hover {
  color: #deb887;
  border: none;
  transform: scale(1.05);
}

.is-hidden {
  display: none;
}

